*,
:after,
:before {
  box-sizing: border-box;
}
.background-image {
  background: url("C:\Users\varsh\Desktop\TO-DO-LIST(react)\background-image.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  width: 100%;
  z-index: 0;
}
.list-style {
  height: 60px;
  margin-left: -30px;
  width: 58%;
}
.main {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%; /* Ensure it takes full height */
  width: 100%; /* Ensure it takes full width */
  position: relative; 
}
.list-style li {
  background-color: #f0f8ff;
  border: none;
  border-radius: 6px;
  font-family: Verdana, Woodchuck Sans-Serif, Tahoma, sans-serif;
  list-style: none;
  margin: 10px;
  padding: 10px;
  text-align: justify;
  width: 85%;
  z-index: 1;
}
.icons {
  background-color: initial;
  border: none;
  cursor: pointer;
  height: 4rem;
  text-align: center;
  transition: transform 0.5 ease-in-out;
  width: 3.5rem;
  z-index: 1;
}
li.checked {
  color: #888;
  text-decoration: line-through;
}
.icons:hover {
  transform: scale(1.5);
}
.box {
  display: flex;
}
.list-style li input {
  margin-right: 10px;
}
.btn {
  border-radius: 5px;
  height: 2.5rem;
  margin-left: 8px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
  width: 4rem;
}
.btn:hover {
  box-shadow: 5px 5px rgba(99, 11, 77, 0.171);
  transform: translateY(-5px);
}
input:not([type="checkbox"]) {
  border: none;
  border-radius: 5px;
  font-family: Verdana, Woodchuck Sans-Serif, Tahoma, sans-serif;
  font-size: 16px;
  height: 40px;
  padding-left: 10px;
  width: 55%;
}
input:not([type="checkbox"]):focus {
  outline: none;
}
.container {
  display: flex;
  justify-content: center;
  width: 90%;
}
h1 {
  font-family: Verdana, Woodchuck Sans-Serif, Tahoma, sans-serif;
  font-weight: 700;
  text-align: center;
}
@keyframes tilt-left-right {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(5deg);
  }
  to {
    transform: rotate(0deg);
  }
}
h1 {
  animation: tilt-left-right 2s ease-in-out infinite;
}
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
}
footer {
  background-color: #333;
  bottom: 0;
  color: #fff;
  font-family: Verdana, Woodchuck Sans-Serif, Tahoma, sans-serif;
  font-size: 14px;
  padding: 20px;
  position: absolute;
  text-align: center;
  width: 100%;
}
@media only screen and (max-width: 480px) {
  .background-image {
    background: url("C:\Users\varsh\Desktop\TO-DO-LIST(react)\background-image-ss.jpg");
    background-size: cover;
    font-size: 14px;
  }
  .list-style {
    height: 40px;
    margin-left: -30px;
    width: 100%;
  }
  input:not([type="checkbox"]) {
    border: 1px solid #888;
    border-radius: 5px;
    font-family: Verdana, Woodchuck Sans-Serif, Tahoma, sans-serif;
    height: 40px;
    padding-left: 10px;
    width: 85%;
  }
  .btn {
    border-radius: 5px;
    margin-left: 8px;
  }
}